<template>
  <div class="popup-container">
    <div class="popup-content">
      <div class="popup-header" v-if="isDouble">
        <div class="cencel" @click="cencelFun()">取消</div>
        <div class="ok" @click="okFun()">确定</div>
      </div>
      <div class="list">
        <div
          class="item"
          :class="selectArr.indexOf(item)>-1?'active-item':''"
          v-for="(item,index) in popupArr"
          :key="index"
          @click="changeFun(item)"
        >{{ item.descript }}</div>
      </div>
    </div>
    <div class="popup-bg" @click="cencelFun()"></div>
  </div>
</template>
<script>
import { Toast } from "mint-ui";
export default {
  props: {
    isDouble: Boolean,
    popupArr: Array
  },
  data() {
    return {
      selectArr: []
    };
  },
  created() {
    this.selectArr = [];
    this.popupArr.forEach(element => {
      if (element.selected) {
        this.selectArr.push(element);
      }
    });
  },
  methods: {
    cencelFun() {
      this.$emit("emitCencelFun");
    },
    okFun() {
      this.$emit("emitOkFun", _.cloneDeep(this.selectArr));
    },
    changeFun(item) {
      if (this.isDouble) {
        let index = this.selectArr.indexOf(item);
        if (index > -1) {
          this.selectArr.splice(index, 1);
        } else {
          if (this.selectArr.length == 2) {
            this.msg(`最多选取${this.selectArr.length}位服务员`);
          } else {
            this.selectArr.push(item);
          }
        }
      } else {
        this.$emit("emitOkFun", _.cloneDeep(item));
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>
<style lang="less" scoped>
.popup-container {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  bottom: 0px;
  z-index: 9;
  .popup-content {
    position: fixed;
    bottom: 0;
    z-index: 1;
    background: #fff;
    width: 100%;

    font-size: 18px;
    color: #333333;
    .popup-header {
      display: flex;
      justify-content: space-between;
      height: 55px;
      line-height: 55px;
      padding-left: 15px;
      padding-right: 15px;
      color: #c4ad68;
    }
    .list {
      max-height: 50vh;
      overflow-y: auto;
      .item {
        height: 55px;
        line-height: 55px;
        box-shadow: inset 0 1px 0 0 #ddd;
      }
      .active-item {
        color: #ccb87c;
      }
    }
  }
  .popup-bg {
    width: 100%;
    height: 100vh;
    position: relative;
    z-index: 0;
    opacity: 0.4;
    background: #000000;
  }
}
</style>



