<template>
  <div class="container-openTable">
    <headerUsually btnShow="true" :titleText="title" :url="gobackurl"></headerUsually>
    <div class="container-nav">
      <span class="restaurant-code">{{selectedTableInfo.code||selectedTableInfo.tableNo}}</span>
      <span class="restaurant-name">{{selectedTableInfo.tableDescript}}</span>
    </div>
    <div class="main-list">
      <div class="item people-num">
        <div class="left">人数</div>
        <div class="right">
          <i class="iconfont icon-jian" @click="cutBackFun()"></i>
          <span class="num">{{peopleNum}}</span>
          <i class="iconfont icon-jia2" @click="addFun()"></i>
        </div>
      </div>
      <div class="item" v-for="(item, index) in list" :key="index" @click="selectFun(index,item)">
        <div class="left">{{item.text}}</div>
        <div class="right">
          <div class="name">
            <template v-if="item.isArry">
              <span v-for="(ele, i) in item.value" :key="i">
                {{ele.code}}
                <span v-if="i!=item.value.length-1">,</span>
              </span>
            </template>
            <template v-else>
              <span>{{item.value.code}}</span>
            </template>
          </div>
          <i class="iconfont icon-jiantou1"></i>
        </div>
      </div>
    </div>
    <div class="btn" @click="gotoFun()" v-if="requiredData.length>0">{{title}}</div>
    <div class="btn btn-disabled" v-else>{{title}}</div>
    <popup
      v-if="isShow"
      :popupArr="popupArr"
      :isDouble="isDouble"
      @emitCencelFun="popupCencelFun()"
      @emitOkFun="popupOkFun($event)"
    ></popup>
  </div>
</template>

<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import popup from "@/components/common/mobileOrdering/popup";
import { mapState, mapGetters } from "vuex";
export default {
  components: {
    headerUsually,
    popup
  },
  data() {
    return {
      list: [
        { text: "服务员", value: [], isArry: true },
        { text: "统计码", value: {}, isArry: false },
        { text: "销售员", value: {}, isArry: false },
        { text: "市场码", value: {}, isArry: false }
      ],
      clickIndex: -1,
      peopleNum: 1,
      typeArr: ["WAITER", "STATIC", "SALER", "MARKET"],
      popupArr: [],
      isShow: false,
      isDouble: true,
      title: "",
      gobackurl: "",
      requiredData: ""
    };
  },
  computed: {
    ...mapState({
      salesPoint: state => state.mobileOrdering.salesPoint,
      selectedTableInfo: state => state.mobileOrdering.selectedTableInfo
    })
  },
  async created() {
    this.getRequiredData();
    if (this.selectedTableInfo.accnt) {
      this.title = "修改";
      this.gobackurl = "/mobileOrdering/myOrder";
      let waiterArr = this.selectedTableInfo.waiter.split(",");
      waiterArr = waiterArr.filter(function(ele) {
        return ele && ele.trim();
      });
      this.list[0].value = waiterArr.map(ele => {
        return {
          code: ele
        };
      });
      this.list[1].value = { code: this.selectedTableInfo.staticItem };
      this.list[2].value = { code: this.selectedTableInfo.saleItem };
      this.list[3].value = { code: this.selectedTableInfo.markItem };
      this.peopleNum = this.selectedTableInfo.gsts;
    } else {
      this.title = "开台";
      this.gobackurl = "/mobileOrdering/index";
      let data = {
        params: {
          pccode: this.selectedTableInfo.pccode
        }
      };
      let res = await ajaxGet("/pos/router?method=pcDefaultStatsis", data);
      if (res && res.resultCode == 0) {
        this.list[1].value = { code: res.data.code };
      }
    }
  },
  methods: {
    cutBackFun() {
      if (this.peopleNum > 1) {
        this.peopleNum--;
      }
    },
    addFun() {
      this.peopleNum++;
    },
    selectFun(index) {
      this.clickIndex = index;
      this.getData(this.typeArr[index]);
    },
    popupCencelFun() {
      this.isShow = false;
    },
    popupOkFun(list) {
      this.list[this.clickIndex].value = list;
      this.isShow = false;
    },
    gotoFun() {
      //0为非必填，1为必填(第一位人数、第二位市场、第三位来源、第三位为统计码、第四位销售员)
      // list: [
      //   { text: "服务员", value: [], isArry: true },
      //   { text: "统计码", value: {}, isArry: false },
      //   { text: "销售员", value: {}, isArry: false },
      //   { text: "市场码", value: {}, isArry: false }

      if (this.requiredData[1] == 1) {
        if (!this.list[3].value.code) {
          this.msg("市场码必选！");
          return;
        }
      }
      if (this.requiredData[2] == 1) {
        if (!this.list[1].value.code) {
          this.msg("统计码必选！");
          return;
        }
      }
      if (this.requiredData[3] == 1) {
        if (!this.list[2].value.code) {
          this.msg("销售员必选！");
          return;
        }
      }
      let waiter = "";
      this.list[0].value.forEach(ele => {
        waiter = waiter + ele.code + ",";
      });
      if (this.selectedTableInfo.accnt) {
        this.modifyTableFun(waiter);
      } else {
        this.openTableFun(waiter);
      }
    },
    async openTableFun(waiter) {
      let data = {
        params: {
          pccode: this.selectedTableInfo.pccode,
          gsts: this.peopleNum,
          tableNo: this.selectedTableInfo.code,
          waiter: waiter,
          staticItem: this.list[1].value.code,
          saleItem: this.list[2].value.code,
          markItem: this.list[3].value.code
        }
      };
      let res = await ajaxGet("/pos/router?method=openCheck", data);
      if (res && res.resultCode == 0) {
        this.selectedTableInfo.accnt = res.data.accnt;
        this.$store.commit("sumbitSelectedTableInfo", this.selectedTableInfo);
        this.$router.push("/mobileOrdering/myOrder");
      }
    },
    async modifyTableFun(waiter) {
      let data = {
        params: {
          accnt: this.selectedTableInfo.accnt,
          gsts: this.peopleNum,
          tableNo: this.selectedTableInfo.code,
          waiter: waiter,
          staticItem: this.list[1].value.code,
          saleItem: this.list[2].value.code,
          markItem: this.list[3].value.code
        }
      };

      let res = await ajaxGet("/pos/router?method=updateCheck", data);
      if (res && res.resultCode == 0) {
        this.$router.push("/mobileOrdering/myOrder");
      }
    },
    async getData(type) {
      console.log("this.selectedTableInfo:", this.selectedTableInfo);
      let data = {
        params: {
          router: type,
          pccode: this.selectedTableInfo.pccode || this.selectedTableInfo.pcCode
        }
      };

      let res = await ajaxGet("/pos/router?method=getOpenCheckBaseData", data);
      if (res && res.resultCode == 0) {
        this.isShow = true;
        this.popupArr = res.data;
        if (this.clickIndex == 0) {
          this.isDouble = true;
          this.popupArr.forEach(ele => {
            this.list[this.clickIndex].value.forEach(opt => {
              if (opt.code == ele.code) {
                this.$set(ele, "selected", true);
              }
            });
          });
        } else {
          this.isDouble = false;
          this.popupArr.forEach(ele => {
            if (this.list[this.clickIndex].value.code == ele.code) {
              this.$set(ele, "selected", true);
            }
          });
        }
      }
    },
    async getRequiredData(type) {
      let data = {
        params: {
          sysCode: "open_item_limit"
        }
      };
      let res = await ajaxGet("/pos/router?method=sysOption", data);
      if (res && res.resultCode == 0) {
        this.requiredData = res.data;
        // console.log("getRequiredData:", res);
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>

<style lang="less">
.container-openTable {
  padding-top: 46px;
  .container-nav {
    height: 60px;
    line-height: 60px;
    width: 100%;
    background: #393c41;
    span {
      font-size: 20px;
      color: #c4ad68;
      margin-right: 4px;
    }
  }
  .main-list {
    padding-left: 18px;
    background-color: #fff;
    .item {
      height: 55px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #ddd;
      padding-right: 17px;
      .left {
        font-size: 14px;
        color: #666666;
      }
      .right {
        display: flex;
        align-items: center;
        font-size: 14px;
        color: #333333;
        .iconfont {
          color: #a8acb8;
        }
      }
    }
    .item:last-child {
      border-bottom: none;
    }
    .people-num {
      .right {
        .iconfont {
          color: #c4ad68;
          font-size: 30px;
        }
        .num {
          margin: 0 10px;
          width: 30px;
          display: inline-block;
        }
      }
    }
  }
  .btn {
    margin-top: 20px;
    background: #ffffff;
    font-size: 18px;
    color: #c4ad68;
    text-align: center;
    padding: 11px;
  }
  .btn-disabled {
    color: #ccc;
  }
}
</style>



